import { Store, useStore } from './Store'
import getsublevel from '../utils/api/getsublevel'
import postsublevel from '../utils/api/postsublevel'
import editsublevel from '../utils/api/editsublevel'
import deletesublevel from '../utils/api/deletesublevel'
import getsublevel_data_byid from '../utils/api/getsublevel_data_byid'
import { logerror } from 'utils/actions'
export class sublevelItem {
  id: String
  sl_types: String
  sl_icon: String
  sl_title: String
  sl_stitle: String
  sl_body: String
  sl_bodytype: String
  sl_bodyoptions: String
  sl_misc: boolean
  created_at: String
  updated_at: String
  instance_id: String
  level_id: String
  sl_media: String
  TextData: any

  constructor(o: {
    id: String
    sl_types: String
    sl_icon: String
    sl_title: String
    sl_stitle: String
    sl_body: String
    sl_bodytype: String
    sl_bodyoptions: String
    sl_misc: boolean
    created_at: String
    updated_at: String
    instance_id: String
    level_id: String
    sl_media: String
    TextData: any
  }) {
    this.id = o.id
    this.sl_types = o.sl_types
    this.sl_icon = o.sl_icon
    this.sl_title = o.sl_title
    this.sl_stitle = o.sl_stitle
    this.sl_body = o.sl_body
    this.sl_bodytype = o.sl_bodytype
    this.sl_bodyoptions = o.sl_bodyoptions
    this.sl_misc = o.sl_misc
    this.created_at = o.created_at
    this.updated_at = o.updated_at
    this.instance_id = o.instance_id
    this.level_id = o.level_id
    this.sl_media = o.sl_media
    this.TextData = o.TextData
  }
}

export const sublevelStore = new Store<sublevelItem[] | false>(false)

export const sublevel1Store = new Store<sublevelItem | false>(false)

export const sublevelstore = () => {
  const [sublevel, setsublevel] = useStore(sublevelStore)
  const [sublevel1, setsublevel1] = useStore(sublevel1Store)

  return {
    sublevel,
    setsublevel,
    sublevel1,
    setsublevel1,
    async loadsublevel() {
      await getsublevel('', '')
        .then(data => {
          setsublevel(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async loadsublevelwithid(instanceid: any, levelid: any) {
      await getsublevel(instanceid, levelid)
        .then(data => {
          setsublevel(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async postsublevel(data: any) {
      await postsublevel(data)
        .then(res => {
          console.log(res)
          setsublevel((oldArray: any) => [...oldArray, res.data])
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async editsublevel(id: any, data: any) {
      await editsublevel(id, data)
        .then(res => {
          console.log(res)
          setsublevel(oldarr => {
            return (
              oldarr &&
              oldarr.map((obj: any) => {
                const getobj = (o: any) => {
                  let x = o
                  x.instance_id = data.instance_id
                  x.level_id = data.level_id
                  x.sl_types = data.sl_types
                  x.sl_icon = data.sl_icon
                  x.sl_title = data.sl_title
                  x.sl_stitle = data.sl_stitle
                  x.sl_body = data.sl_body
                  x.sl_bodytype = data.sl_bodytype
                  x.sl_bodyoptions = data.sl_bodyoptions
                  x.sl_misc = data.sl_misc
                  x.sl_media = data.sl_media
                  return x
                }
                return String(obj.id) === String(id) ? getobj(obj) : obj
              })
            )
          })
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async deletesublevel(id: any) {
      await deletesublevel(id)
        .then(res => {
          console.log(res)
          setsublevel(sublevel && sublevel.filter(ele => ele.id !== id))
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async loadsublevel1_data_byid(id: any) {
      await getsublevel_data_byid(id)
        .then(data => {
          // console.log('getsublevel_data_byid', data)
          setsublevel1(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
